import { Component, OnInit, Input, Output, EventEmitter, ViewChild, Inject, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { MaterialIssueService, MaterialIssue,ErrorHandleService , ItemMasterService, CurrentUserService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxTextAreaComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtextarea';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-ho-material-issue-edit',
  templateUrl: './ho-material-issue-edit.component.html',
  styleUrls: ['./ho-material-issue-edit.component.scss']
})
export class HoMaterialIssueEditComponent implements OnInit {
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('myComboBoxDrCC') myComboBoxDrCC: jqxComboBoxComponent;
  @ViewChild('myComboBox2') myComboBox2: jqxComboBoxComponent;
  @ViewChild('myComboBoxFstore') myComboBoxFstore: jqxComboBoxComponent;
  @ViewChild('aComboBox') aComboBox: jqxComboBoxComponent;
  @ViewChild('typeComboBox') typeComboBox: jqxComboBoxComponent;
  @ViewChild('datetime') datetime: jqxDateTimeInputComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @Input('MaterialIssueEditData') MaterialIssueEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  EditData: MaterialIssue;
  materialIssueForm: FormGroup;
  costCenter_data: any;
  costCenterAdapter: any;
  store_data: any;
  storeAdapter: any;
  source: any;
  dataAdapter: any;
  columns: any[];
  apiUrl: string;
  material_item: Array<any> = [];
  contractorsource: any;
  contractorAdapter: any;
  account_data: any;
  accountAdapter: any;
  itemAdapter: any;
  typeAdapter: any[] = [
    { value: 'sis', label: 'Sister Tea Estate' },
    { value: 'adj', label: 'Adjustment' },
  ];

  toStoreShow:boolean = false;

  userData: any = {};

  constructor(private fb: FormBuilder,
    private cdr: ChangeDetectorRef,
    @Inject('API_URL') apiUrl: string,
    private mis: MaterialIssueService,
    private ehs: ErrorHandleService,
    private itemService: ItemMasterService,
    private cus: CurrentUserService,
  ) {
    this.apiUrl = apiUrl;
    this.userData =  this.cus.getTokenData();
    this.createForm();
  }

  ngOnInit() {
    this.getItem();
    this.costCenter_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'costcenter?limit=all',
      };
    this.costCenterAdapter = new jqx.dataAdapter(this.costCenter_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        if(this.MaterialIssueEditData.cost_center_id){
          this.myComboBox.val(this.MaterialIssueEditData.cost_center_id);
        }
      }
    });
    this.account_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'account_head?limit=all',
      };
    this.accountAdapter = new jqx.dataAdapter(this.account_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        if(this.MaterialIssueEditData.cr_account_head_id){
          this.aComboBox.val(this.MaterialIssueEditData.cr_account_head_id);
        }
      }
    });
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        console.log(this.MaterialIssueEditData);
        if(this.MaterialIssueEditData.to_store_id){
          this.myComboBox2.val(this.MaterialIssueEditData.to_store_id);
        }
        if(this.MaterialIssueEditData.store_id){
          this.myComboBoxFstore.val(this.MaterialIssueEditData.store_id);
        }
      }
    });
    this.EditData = new MaterialIssue(this.MaterialIssueEditData);
    this.materialIssueForm.setValue(this.EditData);
    this.itemService.index({limit:'all'}).subscribe(
      response =>{
        this.itemAdapter = response;
      },
      error => {
        console.log(error);
      }
    );
  }

  /**
   * 
   */
  ngAfterViewInit() {
    console.log(this.MaterialIssueEditData);
    this.jqxLoader.open();
    this.mis.show(this.MaterialIssueEditData.id).subscribe(
      result => {
        if(result['data'] && result['data']['materialIssue'] && result['data']['materialIssue'].type){
          this.typeComboBox.selectItem(result['data']['materialIssue'].type);
        }
        this.material_item = result['data'] && result['data']['materialIssue'] && result['data']['materialIssue'].material_issue_details || [];
        for (var _i = 0; _i < this.material_item.length; _i++) {
          this.material_item[_i]['name'] = this.material_item[_i]['item'] && this.material_item[_i]['item'].name;
        }
        this.source.localdata = this.material_item;
        this.myGrid.updatebounddata("cells");
        this.jqxLoader.close();
      },
      error => {
        console.log(error);
      }
    );
    this.cdr.detectChanges();
  }

  createForm() {
    this.materialIssueForm = this.fb.group({
      'issue_no': [null, Validators.required],
      'issue_date': [null, Validators.required],
      // 'from_store': [null, Validators.required],
      'relate_to': [null],
      'remarks': [null],
    });
  }

      /**
   * Track Change Event of Type Combobox
   * @param  
   */
  typeChange($event){
    let selectedType = this.typeComboBox.getSelectedItem().value;
    switch(selectedType) {
      case 'sis': { 
         this.toStoreShow = true;
         break; 
      }
      case 'adj': { 
        this.toStoreShow = false;
        break; 
      }
      default: { 
         break; 
      } 
   } 
    // switch
  }

  getItem() {
    this.source =
      {
        datatype: 'json',
        datafields:

          [
            { name: 'name', type: 'string' },
            { name: 'item_id', type: 'string' },
            { name: 'quantity', type: 'string' },
            { name: 'rate', type: 'string' },
            { name: 'value', type: 'string' },
            { name: 'consumed_detail', type: 'string' },
            { name: 'dr_account_head_id', type: 'string' },
            { name: 'dr_account_head_name', type: 'string', map: 'dr_account_head>name' },
            { name: 'activity_id', type: 'string' },
          { name: 'actvity_name', type: 'string', map: 'activity>name' },

          ],
      };
    this.dataAdapter = new jqx.dataAdapter(this.source);

    this.columns =
      [
        {
          text: 'Item Name', datafield: 'item_id', displayfield: 'name', columntype: 'combobox', width: 120,
          createeditor: (row: number, column: any, editor: any): void => {
            // let contractorsource =
            //   {
            //     datatype: "json",
            //     contentType: "application/json; charset=utf-8",
            //     datafields: [
            //       { name: 'id' },
            //       { name: 'name' },
            //     ],
            //     async: false,
            //     url: this.apiUrl + 'item?limit=all'
            //   };
            // let contractorAdapter = new jqx.dataAdapter(contractorsource, {
            //   downloadComplete: (data, status, xhr) => {
            //     let tData = data.data;
            //     return tData;
            //   }
            // });
            editor.jqxComboBox({
              source: this.itemAdapter,
              displayMember: "name",
              valueMember: "id",
              searchMode: "containsignorecase"
            });
          },
          cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
            // if (newvalue != oldvalue) {
            console.log(newvalue);
            let currentRow = this.myGrid.getrowdata(row).boundindex;
            this.itemService.show(newvalue['value']).subscribe(
              result => {
                console.log(result)
                if (result['data']['quantity']) {
                  this.myGrid.setcellvalue(currentRow, 'quantity', result['data'].quantity);
                } else {
                  this.myGrid.setcellvalue(currentRow, 'quantity', 0);
                }

                if (result['data']['rate']) {
                  this.myGrid.setcellvalue(currentRow, 'rate', result['data'].rate);
                } else {
                  this.myGrid.setcellvalue(currentRow, 'rate', 0);
                }

                if(result['data']['quantity'] && result['data']['rate']){
                  let val = Number(result['data']['rate'])*Number(result['data']['quantity']);
                  this.myGrid.setcellvalue(currentRow, 'value', val.toFixed(2));
                }

              },
              error => {
                console.log(error);
              }
            )
            // }
          },
        },
        { text: 'Quantity', datafield: 'quantity', columntype: 'textbox', width:80,
          cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
            if (newvalue !== oldvalue) {
              let currentRow = this.myGrid.getrowdata(row);
              let rowId = this.myGrid.getrowdata(row).boundindex;
              currentRow['quantity'] = newvalue;
              this.amountCalculation(currentRow, rowId);
            }
          },
        },
        { text: 'Rate', datafield: 'rate', columntype: 'textbox', width:90,
          cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
            if (newvalue !== oldvalue) {
              let currentRow = this.myGrid.getrowdata(row);
              let rowId = this.myGrid.getrowdata(row).boundindex;
              currentRow['rate'] = newvalue;
              this.amountCalculation(currentRow, rowId);
            }
          },
        },
        {
          text: 'Value', datafield: 'value', columntype: 'textbox', editable: false, width: 90,
          // cellsrenderer: (index: number, datafield: number, value: any, defaultvalue: any, column: any, rowdata: any): string => {
          //   if (rowdata.quantity && rowdata.rate) {
          //     let total = parseFloat(rowdata.quantity) * parseFloat(rowdata.rate);
          //     return '<div style="float: left; margin: 4px;">' + this.dataAdapter.formatNumber(total, 'f2') + '</div>';
          //   }
          // },
        },
        { text: 'Consumed Detail', datafield: 'consumed_detail', columntype: 'textbox' },
        {
          text: 'Dr Account Head', datafield: 'dr_account_head_id', displayfield: 'dr_account_head_name', columntype: 'combobox', createeditor: (row: number, column: any, editor: any): void => {
            let contractorsource =
              {
                datatype: "json",
                contentType: "application/json; charset=utf-8",
                datafields: [
                  { name: 'id' },
                  { name: 'name' },
                ],
                async: false,
                data: this.userData,
                url: this.apiUrl + 'account_head?limit=all'
              };
            let contractorAdapter = new jqx.dataAdapter(contractorsource, {
              downloadComplete: (data, status, xhr) => {
                let tData = data.data;
                return tData;
              }
            });
            editor.jqxComboBox({
              source: contractorAdapter,
              displayMember: "name",
              valueMember: "id"
            });
          }
        },
        {
          text: 'Activity', datafield: 'activity_id', displayfield: 'activity_name', width: 160, columntype: 'combobox', createeditor: (row: number, column: any, editor: any): void => {
            let contractorsource =
              {
                datatype: "json",
                contentType: "application/json; charset=utf-8",
                datafields: [
                  { name: 'id' },
                  { name: 'name' },
                ],
                async: false,
                data: this.userData,
                url: this.apiUrl + 'activity_master?limit=all'
              };
            let contractorAdapter = new jqx.dataAdapter(contractorsource, {
              downloadComplete: (data, status, xhr) => {
                let tData = data.data;
                return tData;
              }
            });
            editor.jqxComboBox({
              source: contractorAdapter,
              displayMember: "name",
              valueMember: "id",
              searchMode: "containsignorecase"
            });
          }
        },
        {
          text: 'Actions', datafield: 'Delete', columntype: 'button', width: 70,
          cellsrenderer: (): string => {
            return 'Delete';
          },
          buttonclick: (row: number): void => {
            let rowid = this.myGrid.getrowid(row)
            this.myGrid.deleterow(rowid);
          }
        }
      ];

  }

  amountCalculation(data, rowid){
    if(data){
      let value:number = 0;
      if(data['quantity'] && data['rate']){
        value = data['quantity'] * data['rate'];
      }
      this.myGrid.setcellvalue(rowid,'value',value);
    }
  }

  // rendertoolbar = (toolbar: any): void => {
  //   let container = document.createElement('div');
  //   container.style.margin = '5px';

  //   let headingContainer4 = document.createElement('div');
  //   let buttonContainer5 = document.createElement('div');

  //   headingContainer4.id = 'headingContainer4';
  //   buttonContainer5.id = 'buttonContainer5';

  //   headingContainer4.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
  //   buttonContainer5.style.cssText = 'float: right';

  //   headingContainer4.innerHTML = 'Material Issue Item Details:';

  //   container.appendChild(headingContainer4);
  //   container.appendChild(buttonContainer5);
  //   toolbar[0].appendChild(container);

  //   let addRowButton = jqwidgets.createInstance('#buttonContainer5', 'jqxButton', { width: 155, value: 'Add Material Issue Item' });


  //   addRowButton.addEventHandler('click', () => {
  //     let datarow = this.generaterow();
  //     this.myGrid.addrow(null, datarow);
  //   })

  // };

  addNewItem(){
    let datarow = this.generaterow();
    this.myGrid.addrow(null, datarow);
  }


  generaterow(): any {
    let row = {};
    row['item_id'] = '';
    row['quantity'] = '';
    row['rate'] = '';
    row['value'] = '';
    row['dr_account_head_id'] = '';
    row['activity_id'] = '';
    row['consumed_detail'] = '';
    row['remarks'] = '';
    return row;
  }

  saveBtn(post) {
    post['cr_cost_center_id'] = this.myComboBox.getSelectedItem() && this.myComboBox.getSelectedItem().value;
    post['dr_cost_center_id'] = this.myComboBoxDrCC.getSelectedItem() && this.myComboBoxDrCC.getSelectedItem().value;
    post['store_id'] = this.myComboBox2.getSelectedItem() && this.myComboBox2.getSelectedItem().value;
    post['cr_account_head_id'] = this.aComboBox.getSelectedItem() && this.aComboBox.getSelectedItem().value;
    post['materialIssueDetails'] = this.myGrid.getrows();

    this.myValidator.validate(document.getElementById('materialIssueForm'));
    if (post['cr_account_head_id'] != undefined && post['cr_cost_center_id'] != undefined && post['materialIssueDetails'] != undefined && post['type']) {
      this.jqxLoader.open();
      this.mis.update(this.MaterialIssueEditData.id, post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        });

    }
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.issue_noInput', message: 'Issue no is required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.issue_dateInput', message: 'Issue date is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.datetime.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.costcenter_idInput', message: 'Cost center name is required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.myComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.type', message: 'Type is required !', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.typeComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      // {
      //   input: '.store_idInput', message: 'Store name is required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
      //     let data = this.myComboBox2.val();
      //     let result = (data != '') ? true : false;
      //     return result;
      //   }
      // },
      {
        input: '.cr_account_head_idInput', message: 'Cr account head is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.aComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.from_store', message: 'From Store is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.myComboBoxFstore.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      // { input: '.relate_toInput', message: 'Relate to is required!', action: 'keydown, blur', rule: 'required' },
      // { input: '.remarksInput', message: 'Remark is required!', action: 'keydown, blur', rule: 'required' },
    ];

}



