import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { HoMaterialIssueRoutingModule } from './ho-material-issue-routing.module';
import { HoMaterialIssueComponent } from './ho-material-issue.component';
import { HoMaterialIssueAddComponent } from './ho-material-issue-add/ho-material-issue-add.component';
import { HoMaterialIssueEditComponent } from './ho-material-issue-edit/ho-material-issue-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    HoMaterialIssueRoutingModule
  ],
  entryComponents:[
    HoMaterialIssueAddComponent,
    HoMaterialIssueEditComponent,
  ],
  declarations: [
    HoMaterialIssueComponent,
    HoMaterialIssueAddComponent,
    HoMaterialIssueEditComponent,
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class HoMaterialIssueModule { }



