import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HoMaterialIssueComponent } from './ho-material-issue.component';

const routes: Routes = [
    {
        path: '', component: HoMaterialIssueComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HoMaterialIssueRoutingModule { }



